@import '../styles/variables';

.widget-cta {
    border-top: 1px solid $whitegrey-border;
    margin-top: .5rem;
    padding-top: 1rem;
    width: 100%;
}

.bc-btn--tertiary.btn-blue-link {
    color: var(--c-blue-mid-3);
    font-weight: 700;
    margin-left: -1rem;

    &:hover:not(:disabled) {
        border-color: var(--c-blue-mid-3);
        color: var(--c-blue-mid-3);
    }

    .bc-icon--right {
        height: 1.5rem;
        width: 1.5rem;

        &:before {
            font-size: 1rem;
            font-weight: 700;
        }
    }
}