@import '../../styles/variables';
@import '../../styles/mixin';

.bc-header {
    /* Top Header */
    .bc-header__top {
        .bc-header__top__link {
            &.notification__link {
                @include flex-center;
                
                .notification__icon {
                    margin-right: .4rem;
                    position: relative;

                    .has_notification {
                        background-color: var(--c-error-bg);
                        border-radius: 100%;
                        height: .6rem;
                        position: absolute;
                        right: 0;
                        top: 0;
                        width: .6rem;
                    }
                }
                
            }
        }

        .bc-dropdown {
            .bc-dropdown-trigger {
                @include flex-center;

                .bc-icon-mail,
                .bc-icon-user {
                    margin-right: .4rem;
                }

                .bc-icon-direction-down-fat {
                    margin-left: .4rem;
                }

                &.active {
                    .bc-icon-direction-down-fat {
                        transform: rotate(180deg);
                    }
                }
            }

            .bc-dropdown__content--right {
                padding: 1.5rem;

                h2 {
                    color: var(--c-default-text);
                    font-size: 1rem;
                    margin: 0;
                }

                .bc-dropdown__content--contacts {
                    display: flex;

                    div:first-child {
                        border-right: 1px solid $whitegrey-border;
                        margin-right: 2rem;
                        padding-right: 2rem;
                    }

                    h3 {
                        font-size: 1.125rem;
                        margin: 0;
                    }

                    .title {
                        color: var(--c-details);
                        font-size: .875rem;
                        margin: .5rem 0 0;

                        strong {
                            display: block;
                            font-weight: 400;
                            text-transform: uppercase;
                        }
                    }

                    .contact-list {
                        margin-top: .5rem;

                        li {
                            margin-right: .5rem;
                            min-width: 10rem;
                        }
                    }
                }

                &.profile-dropdown {
                    background-color: var(--c-white);
                    border: .5px solid $grey-2-border;
                    border-top: none;
                    border-radius: 0 0 5px 5px;
                    margin-top: 0;

                    ul {
                        margin: 0;

                        li {
                            margin-bottom: .8rem;

                            a {
                                @include flex-center;
                                justify-content: flex-start;
                                text-decoration: none;
    
                                .icon-img {
                                    margin-right: .4rem;
                                }
    
                                &:last-child {
                                    margin-bottom: 0;
                                }

                                &:hover {
                                    text-decoration: underline;
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    /* Bottom Links */
    .bc-header__bottom {
        .bc-header__bottom-end {
            .bc-header__bottom__link {
                .link__label {
                    align-items: center;
                    display: flex;
                    margin: 0;
                    white-space: nowrap;

                    .bc-icon-direction-down-fat {
                        color: #50616A;
                        font-size: 1rem;
                        margin-bottom: 0;
                        margin-left: .4rem;
                    }
                }

                & + .bc-header__bottom__sublinks {
                    background-color: var(--c-default-bg);
                    z-index: 9;

                    .bc-cols {
                        width: 100%;

                        /* Menu */
                        .sublinks__menu {
                            background-color: var(--c-blue-lighter);
                            padding-right: 0;

                            .sublink {
                                display: flex;
                                flex-direction: column;
                                position: relative;
                                text-decoration: none;
                                transition: .3s all;
                                border-radius: .5rem 0px 0px .5rem;
                                padding: .9rem 1.4rem .9rem 4.1rem;

                                img {
                                    left: 1.4rem;
                                    position: absolute;
                                    top: .9rem;
                                }

                                .sublink__label {
                                    font-size: .85rem;
                                    font-weight: 600;
                                }

                                .sublink__description {
                                    color: var(--c-details);
                                    font-size: .8rem;
                                }

                                &:hover {
                                    background-color: var(--c-default-bg);
                                }
                            }
                        }

                        /* Content */
                        .sublinks__content {
                            .sublinks__content-item {
                                display: none;

                                &.show {
                                    display: block;
                                }

                                h2 {
                                    color: var(--c-details);
                                    font-size: .9rem;
                                    margin-bottom: 1rem;
                                }

                                .bc-badge {
                                    border-radius: 8px;
                                    color: $grey-4-color;
                                    font-size: .7rem;
                                    line-height: 1.3rem;
                                    padding: 0 .7rem;

                                    &.blue-badge {
                                        background-color: $ice-blue-background;
                                    }

                                    &.green-badge {
                                        background-color: $sea-green-background;
                                    }
                                }
                                
                                ul {
                                    padding-left: 0;

                                    .sublinks__content-list-item {
                                        list-style: none;
                                        margin-top: .8rem;
                                        padding: .7rem 0;

                                        a {
                                            align-items: flex-start;
                                            display: flex;
                                            text-decoration: none;

                                            h3 {
                                                font-size: .8rem;
                                                font-weight: 600;
                                                margin: 0;
                                            }

                                            p {
                                                color: var(--c-details);
                                                font-size: .8rem;
                                                margin: 0;
                                            }

                                            .img-left {
                                                margin-right: .9rem;
                                            }

                                            .img-right {
                                                margin-left: auto;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }

                /* Sub menu */
                &:hover {
                    .bc-icon-direction-down-fat {
                        color: var(--c-blue-mid-3);
                    }

                    &:not(.active) {
                        & + .bc-header__bottom__sublinks {
                            height: 0;
                        }
                    }
                }

                &:focus {
                    .bc-icon-direction-down-fat {
                        color: var(--c-blue-mid-3);
                    }

                    & + .bc-header__bottom__sublinks {
                        height: 0;
                    }
                }

                &.active {
                    .bc-icon-direction-down-fat {
                        transform: rotate(180deg);
                    }
                    
                    & + .bc-header__bottom__sublinks {
                        height: auto;
                    }

                    &:focus {
                        & + .bc-header__bottom__sublinks {
                            height: auto;
                        }
                    }
                }
            }
            .bc-header__bottom__actions {
                flex: 1 0 auto;
                max-width: 19rem;

                .bc-input {
                    width: 100%;

                    input {
                        background-color: var(--c-grey-tr-2);
                        border: none;    

                        &::placeholder {
                            opacity: 1;
                        }
                        
                        &:focus:not(:disabled):not(:read-only), 
                        &:not(:placeholder-shown) {
                            padding-top: 0;
                        }
                    }
                }
            }
        }
    }
}