@import '../../../../styles/variables';

.visited-list {
    display: flex;
    flex-wrap: wrap;

    li {
        margin: .5rem 2rem .5rem 0;
    
        a {
            color: var(--c-blue-mid-3);
            display: flex;
            font-weight: 700;  
            transition: .3s all;
        }
    }
}