}
:root {
  /* ==== COLORS ==== */
  --c-grey-dark-2: #0b353d;
  --c-grey-dark-1: #394d56;
  --c-grey: #55626a;
  --c-grey-light-1: #7c878c;
  --c-grey-light-2: #a4a8ad;
  --c-grey-tr-1: #f5f5f5;
  --c-grey-tr-2: #ebebeb;
  --c-grey-tr-3: #e0e0e0;
  --c-grey-tr-4: #d9d9d9;
  --c-grey-tr-11: #8f8f8f;
  --c-grey-tr-14: #707070;
  --c-black: #000000;
  --c-black-9: #252525;
  --c-black-8: #464646;
  --c-black-7: #636363;
  --c-black-6: #7d7d7d;
  --c-white: #f0f1f2;
  --c-blue-dark: #182349;
  --c-blue-mid-1: #143db2;
  --c-blue-mid-2: #2163ce;
  --c-blue-mid-3: #007da4;
  --c-blue-light: #4bb7e2;
  --c-blue-lighter: #f5fafb;
  --c-blue-tr-1: #d6ebf0;
  --c-blue-tr-2: #b0d6df;
  --c-green-sea: #00cc99;
  --c-order: #2ad12a;
  --c-order-hover: #008000;
  --c-success-bg: #008000;
  --c-success-text: #008000;
  --c-warning-bg: #f58221;
  --c-warning-text: #a55d00;
  --c-error-bg: #b30000;
  --c-error-text: #b30000;
  --c-gr-blue-dark: linear-gradient(90deg, var(--c-blue-dark, #182349) 50%, #ffffff 100%);
  --c-gr-blue-mid-1: linear-gradient(90deg, var(--c-blue-mid-1, #143db2) 50%, #ffffff 100%);
  --c-gr-blue-mid-2: linear-gradient(90deg, var(--c-blue-mid-2, #2163ce) 50%, #ffffff 100%);
  --c-gr-blue-mid-3: linear-gradient(90deg, var(--c-blue-mid-3, #007da4) 50%, #ffffff 100%);
  --c-gr-blue-light: linear-gradient(90deg, var(--c-blue-light, #4bb7e2) 50%, #ffffff 100%);
  --c-default-text: #0a0a0a;
  --c-heading: var(--c-grey, #55626a);
  --c-hover: #0082a0;
  --c-visited: #800080;
  --c-details: #707070;
  --c-default-bg: #ffffff;
  --c-1: #f5f5f5;
  --c-2: #ebebeb;
  --c-3: #e0e0e0;
  --c-blur: #6b6b6b;
  --c-emphasis: #d6ebf0;
  /* ==== FONTS ==== */
  --font-size-base: 16px;
  --font-primary: "IBM Plex Sans", sans-serif;
  /* ==== SHADOWS ==== */
  --shadow-default: 0px 10px 30px rgba(85, 98, 106, 0.32);
  --shadow-box: 0px 3px 6px rgba(0, 0, 0, 0.15);
  --shadow-emphasis: 0px 30px 45px rgba(0, 0, 0, 0.12);
  --shadow-btn-primary: 0px 12px 10px -8px rgba(0, 194, 255, 0.16), 0px 8px 10px -2px rgba(39, 100, 119, 0.16),
    0px 10px 30px rgba(44, 106, 125, 0.24);
  --shadow-btn-secondary: 0px 12px 10px -8px rgba(31, 123, 144, 0.16), 0px 8px 10px -2px rgba(233, 236, 237, 0.16),
    0px 10px 30px rgba(82, 137, 150, 0.24);
  --shadow-btn-order: 0px 12px 10px -8px rgba(0, 0, 0, 0.08), 0px 8px 10px -2px rgba(0, 0, 0, 0.08),
    0px 10px 30px rgba(0, 0, 0, 0.12);
  /* ==== BORDERS ==== */
  --border-default: solid 1px var(--c-grey-tr-3);
  --radius: 5px;
  --radius-sm: 2px;
  --radius-circle: 9999px;
  /* ==== TRANSITION ==== */
  --transition: 0.3s cubic-bezier(0.72, 0.26, 0.26, 0.82);
}
:root .dark-mode-to-be-setup {
  --c-success-bg: #2ad12a;
  --c-success-text: #2ad12a;
  --c-warning-bg: #f5ab35;
  --c-warning-text: #f5ab35;
  --c-error-bg: #f22613;
  --c-error-text: #f22613;
}
:root .dark-mode-to-be-setup {
  --c-default-text: #ffffff;
  --c-heading: var(--c-white, #ffffff);
  --c-hover: #0082a0;
  --c-visited: #800080;
  --c-details: #a3a3a3;
  --c-default-bg: #000000;
  --c-1: #141414;
  --c-2: #1f1f1f;
  --c-3: #292929;
  --c-blur: #cccccc;
  --c-emphasis: #3d3d3d;
}
:root .dark-mode-to-be-setup {
  --shadow-default: 0px 10px 30px rgba(0, 0, 0, 0.32);
}

