/* ======================== BASE.SCSS ======================== */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* light mode */
body {
  font-family: var(--font-primary, "IBM Plex Sans", sans-serif);
  color: var(--c-default-text, inherit);
  font-size: 1rem;
  line-height: 1.5;
  font-weight: 400;
}

section,
article {
  font-family: inherit;
}

p,
.bc-text-p {
  font-size: 1rem;
  margin-top: 0.5em;
  margin-bottom: 0.5em;
}

small,
.bc-small {
  color: var(--c-details, inherit);
  font-size: 0.875rem;
}

b,
strong,
.bc-strong {
  font-weight: 700;
}

a {
  color: inherit;
  text-decoration: underline;
}
a:hover, a:focus {
  color: var(--c-hover, inherit);
}

h1,
.bc-text--h1,
h2,
.bc-text--h2,
h3,
.bc-text--h3,
h4,
.bc-text--h4,
h5,
.bc-text--h5,
h6,
.bc-text--h6,
.bc-text--subtitle {
  font-weight: 700;
  line-height: 1.3;
  margin-top: 1.5em;
  margin-bottom: 0.5em;
  color: var(--c-heading, inherit);
}

h1,
.bc-text--h1 {
  font-size: 1.875rem;
}
@media only screen and (min-width: 1024px) {
  h1,
.bc-text--h1 {
    font-size: 2.625rem;
  }
}

h2,
.bc-text--h2 {
  font-size: 1.625rem;
}
@media only screen and (min-width: 1024px) {
  h2,
.bc-text--h2 {
    font-size: 2.25rem;
  }
}

h3,
.bc-text--h3 {
  font-size: 1.375rem;
}
@media only screen and (min-width: 1024px) {
  h3,
.bc-text--h3 {
    font-size: 1.75rem;
  }
}

h4,
.bc-text--h4 {
  font-size: 1.25rem;
}
@media only screen and (min-width: 1024px) {
  h4,
.bc-text--h4 {
    font-size: 1.5rem;
  }
}

h5,
.bc-text--h5 {
  font-size: 1.125rem;
}
@media only screen and (min-width: 1024px) {
  h5,
.bc-text--h5 {
    font-size: 1.25rem;
  }
}

h6,
.bc-text--h6 {
  font-size: 1rem;
}
@media only screen and (min-width: 1024px) {
  h6,
.bc-text--h6 {
    font-size: 1.125rem;
  }
}

.bc-text--subtitle {
  font-weight: 300;
  font-size: 1.25rem;
  margin-top: -0.5em;
}
@media only screen and (min-width: 1024px) {
  .bc-text--subtitle {
    font-size: 1.5rem;
  }
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

