/* override storybook css */
.bc-storybook .sb[class^=bc-p], .bc-storybook .sb[class*=" bc-p"], .bc-storybook .sb[class^=bc-m], .bc-storybook .sb[class*=" bc-m"],
.docs-story .sb[class^=bc-p],
.docs-story .sb[class*=" bc-p"],
.docs-story .sb[class^=bc-m],
.docs-story .sb[class*=" bc-m"] {
  background-color: var(--c-blue-tr-2);
}
.bc-storybook .sb[class^=bc-p] span, .bc-storybook .sb[class*=" bc-p"] span, .bc-storybook .sb[class^=bc-m] span, .bc-storybook .sb[class*=" bc-m"] span,
.docs-story .sb[class^=bc-p] span,
.docs-story .sb[class*=" bc-p"] span,
.docs-story .sb[class^=bc-m] span,
.docs-story .sb[class*=" bc-m"] span {
  display: block;
  text-align: center;
  background-color: white;
  filter: grayscale(1);
  border: solid 1px var(--c-blue-tr-2);
}
.bc-storybook .sb[class^=bc-p]::before, .bc-storybook .sb[class*=" bc-p"]::before, .bc-storybook .sb[class^=bc-m]::before, .bc-storybook .sb[class*=" bc-m"]::before,
.docs-story .sb[class^=bc-p]::before,
.docs-story .sb[class*=" bc-p"]::before,
.docs-story .sb[class^=bc-m]::before,
.docs-story .sb[class*=" bc-m"]::before {
  content: "";
  position: absolute;
  top: 40px;
  right: 30px;
  bottom: 40px;
  left: 30px;
  background-color: rgba(0, 0, 250, 0.04);
  border: solid 1px var(--c-blue-tr-2);
}

