/* ==== BADGE ==== */
.bc-badge {
  color: var(--c-grey);
  padding: 0.25em 0.5em;
  border-radius: var(--radius);
  background-color: var(--c-blue-tr-1);
  white-space: nowrap;
  display: inline-flex;
  align-items: center;
}
.bc-badge + .bc-badge {
  margin-left: 0.5rem;
}

/* ==== BULLET LIST ==== */
.bc-bullet-list {
  display: flex;
  justify-content: center;
}
.bc-bullet-list > span {
  display: block;
  height: 0.5rem;
  width: 3rem;
  border-radius: var(--radius-circle);
  background-color: var(--c-blue-mid-3);
  opacity: 0.2;
  cursor: pointer;
}
.bc-bullet-list > span.active {
  opacity: 1;
}
.bc-bullet-list > span + span {
  margin-left: 0.5rem;
}

/* ==== BUTTON ==== */
[class^=bc-btn],
[class*=" bc-btn"] {
  font-family: var(--font-primary);
  font-weight: 700;
  border: solid 2px transparent;
