@import '../../styles/variables';

.dashboard-title {
    color: var(--c-blue-mid-3);
    font-size: 2.25rem;
    margin: 2.7rem 0;
}

/* Widget generic style */

.widget {
    align-items: flex-start;
    background: $widget-backgrond-color;
    border-radius: 1rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin-bottom: 2rem;
    padding: 1.5rem 1rem 1.5rem 1.5rem;
    width: 100%;

    h4 {
        color: $grey-4-color;
        font-size: 1.25rem;
        margin: 0;
    }
}