@import 'variables';

/* ============================== */
/* Scrollbar                      */
/* ============================== */

/* Firefox */
* {
    scrollbar-width: thin;
    scrollbar-color: #50616A #A4B5BB;
  }
  
/* Chrome, Edge and Safari */

*::-webkit-scrollbar {
    width: 10px;
    width: 10px;
}

*::-webkit-scrollbar-track {
    border-radius: 5px;
    background-color: #A4B5BB;
}

*::-webkit-scrollbar-track:hover {
    background-color: #A4B5BB;
}

*::-webkit-scrollbar-track:active {
    background-color: #A4B5BB;
}

*::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: #50616A;
}

*::-webkit-scrollbar-thumb:hover {
    background-color: #50616A;
}

*::-webkit-scrollbar-thumb:active {
    background-color: #50616A;
}

/* ============================== */
/* Override default styles        */
/* ============================== */

ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

p {
    margin: 0;
}

a {
    text-decoration: none;
}

button {
    background: none;
    border: none;
    cursor: pointer;
}

/* ============================== */
/* New style                      */
/* ============================== */

.highlights {
    background-color: var(--c-white);
    border-radius: 8px;
    color: var(--c-black);
    font-size: .875rem;
    padding: .25rem;
}

.light-subtitle {
    color: var(--c-black-6);
}

.link-arrow-right {
    background-color: $light-blue-arrow-background;
    border-radius: 100%;
    display: block;
    height: 1.5rem;
    margin-left: .5rem;
    position: relative;
    width: 1.5rem;

    img {
        left: 50%;
        position: absolute;
        top: 50%;
        transform: translate(-50%, -50%);
    }
}

.warning-text {
    color: var(--c-warning-text);
}

.balance-text {
    color: $grey-4-color;

    strong {
        display: block;
        font-size: 1.5rem;
        font-weight: 700;
        text-transform: uppercase;
    }
}

/* Close button */

.close-btn {
    background-color: $close-icon-background;
    border-radius: 100%;
    box-shadow: 0px 3px 6px 0px rgba(0, 0, 0, 0.15);
    height: 2rem;
    width: 2rem;
}


/* ============================== */
/* Generic Numbers list           */
/* ============================== */

.numbers-list {
    display: flex;
    justify-content: space-between;
    width: 100%;

    li {
        color: var(--c-black-9);
        flex: 0 0 33.3%;

        span {
            color: var(--c-blue-mid-3);
            display: block;
            font-size: 2.8rem;
            font-weight: 700;
            line-height: initial;
        }

        &:nth-child(2) {
            flex: 0 1 auto;
        }

        &:last-child {
            text-align: right;
        }
    }
}