@import '../../../../styles/variables';
@import '../../../../styles/mixin';


.notifications-list {
    background-color: $primary-grey-black;
    border-radius: 1rem;
    max-height: 50rem;
    overflow-y: auto;
    padding: .5rem;
    width: 100%;

    .notification-card {
        background-color: var(--c-default-bg);
        border-radius: 1rem;
        margin-bottom: 1rem;
        padding: 1rem;

        .notification-top {
            @include flex-space-between;

            .notification-date {
                color: var(--c-details);
                font-size: .8rem;
            }
        }

        .notification-content {
            .notif-icon {
                flex: 1 0 2.25rem;
            }

            .notification-content-text {
                padding-left: .5rem;

                h5 {
                    margin-top: 0;
                }
            }
        }

        &:last-child {
            margin-bottom: 0;
        }
    }
}